"use client";

// src/app/homebanner/page.js

function formatMMDD(date) {
  const mm = String(date.getMonth() + 1).padStart(2, "0");
  const dd = String(date.getDate()).padStart(2, "0");
  return `${mm}/${dd}`;
}


/**
 * Ïù¥Î≤à Ï£º(Ïõî~Ïùº) ÎÇ†Ïßú Î∞∞Ïó¥ ÏÉùÏÑ±
 */
function getThisWeekDates() {
  const today = new Date();
  const day = today.getDay(); // 0=ÏùºÏöîÏùº

  // ÏõîÏöîÏùº Í∏∞Ï§ÄÏúºÎ°ú ÎßûÏ∂îÍ∏∞
  const diffToMonday = (day === 0 ? -6 : 1) - day;

  const monday = new Date(today);
  monday.setDate(today.getDate() + diffToMonday);

  // Ïõî~Ïùº 7Ïùº ÏÉùÏÑ±
  const dates = [];
  for (let i = 0; i < 7; i++) {
    const d = new Date(monday);
    d.setDate(monday.getDate() + i);
    dates.push(d);
  }

  return dates;
}

export default function HomeBannerSchedulePage() {
  const dates = getThisWeekDates();

  // ÏàúÎ≤à 1~7
  const ranks = Array.from({ length: 7 }, (_, i) => i + 1);

  return (
    <main style={{ padding: "40px", fontFamily: "sans-serif" }}>
      <h1 style={{ fontSize: "24px", marginBottom: "20px" }}>
        üìÖ ÌôàÎ∞∞ÎÑà Ï£ºÍ∞Ñ Ïä§ÏºÄÏ•¥
      </h1>

      <table
        border="1"
        cellPadding="15"
        style={{
          borderCollapse: "collapse",
          width: "100%",
          textAlign: "center",
        }}
      >
        {/* Ìó§Îçî */}
        <thead style={{ backgroundColor: "#f3f3f3" }}>
          <tr>
            <th>ÏàúÎ≤à</th>
            {dates.map((d, idx) => (
              <th key={idx}>{formatMMDD(d)}</th>
            ))}
          </tr>
        </thead>

        {/* Î∞îÎîî */}
        <tbody>
          {ranks.map((rank) => (
            <tr key={rank}>
              <td style={{ fontWeight: "bold" }}>{rank}</td>

              {dates.map((_, idx) => (
                <td key={idx} style={{ color: "#999" }}>
                  ‚Äî
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>

      <p style={{ marginTop: "20px", color: "gray" }}>
        ÌòÑÏû¨Îäî DB ÏóÜÏù¥ Îπà ÌëúÎßå ÌëúÏãúÎê©ÎãàÎã§.
      </p>
    </main>
  );
}
